import { Component,Input } from '@angular/core';

// Define second directive.
@Component({
    // Selector uses lower case with hyphens.
    selector: 'buildby',
    template: `<hr/>
    <h3 style="text-align:center;">This app is built by {{name}} . </h3>`,
})
export class BuildBy {
    @Input()
    name: string;
}
